import Repository from "./Repository";

const resource = "/auth";

export default {
  login(payload) {
    return Repository.post(`${resource}/login`, payload)
      .then((resp) => {
        return resp.data;
      })
      .catch((err) => {
        return err.response;
      });
  },
  me() {
    return Repository.get(`${resource}/me`)
      .then((resp) => {
        return resp.data;
      })
      .catch((err) => {
        return err.response;
      });
  },
  register(request) {
    return Repository.post(`${resource}/register`, request)
      .then((resp) => {
        return resp.data;
      })
      .catch((err) => {
        return err.response;
      });
  },
};
