<template>
  <div>
    <b-container fluid style="padding: 0">
      <b-row>
        <b-col xs="0" sm="0" md="6" lg="6" xl="6" class="d-none d-sm-block">
          <b-img style="height: 100vh" src="images/login-background.jpeg" fluid-grow alt="Fluid-grow image"></b-img>
        </b-col>
        <b-col xs="12" sm="12" md="4" lg="4" xl="4" class="p-5">
          <b-container>
            <b-row>
              <h1 style="font-size: 1.25rem; font-weight: 700;">
                Đăng nhập
              </h1>
            </b-row>

            <b-row class="mt-4">
              <span style="color: #4a5568;">
                Email
              </span>
              <b-form-input class="mt-3" size="lg" v-model="user.username" placeholder="Nhập email"></b-form-input>
            </b-row>

            <b-row class="mt-4">
              <span style="color: #4a5568;">
                Mật khẩu
              </span>
              <b-form-input class="mt-3" size="lg" type="password" v-model="user.password" placeholder="Nhập mật khẩu"></b-form-input>
            </b-row>

            <b-row class="mt-4">
              <span>Bạn chưa có tài khoản? <router-link to="register">Đăng ký</router-link></span>
            </b-row>

            <b-row class="mt-4">
              <b-button variant="info" size="lg" style="width: 100%;" @click="login">Đăng nhập</b-button>
            </b-row>
          </b-container>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import { RepositoryFactory } from "../repository/RepositoryFactory";
const AuthRepository = RepositoryFactory.get("auth");

export default {
  data() {
    return {
      user: {
        username: "",
        password: "",
      },
    };
  },
  methods: {
    async login() {
      let error = this.checkLogin();

      if (error.length > 0) {
        this.alert("error", error);
        return;
      }
      const response = await AuthRepository.login(this.user);
      if (response.status == "success") {
        localStorage.setItem("userToken", response.data.accessToken);
        this.app.user = response.data.user;
        this.alert("success", "Đăng nhập thành công");

        this.$router.push("/");
      } else {
        this.alert("error", response.data.error.message);
      }
    },
    checkLogin() {
      if (!this.user.username || !this.user.password) {
        return "Vui lòng điền đầy đủ thông tin";
      }
      return "";
    },
  },
};
</script>
